import React, { useState, useEffect } from "react";
import { Box, Paragraph, Select } from "grommet";

const Component = ({ list, onSelectRole, initialRoleIndex }) => {

  return (
    <>
      <Paragraph margin="xxsmall">Interact As:</Paragraph>
      {
        list && <Select
          value={initialRoleIndex}
          placeholder="Chat as"
          options={list}
          labelKey="label"
          valueKey="value"
          onChange={({ option }) => {
            onSelectRole && onSelectRole(option)
          }}
      />
    }
    </>
  );
};

export default Component;
