import React, { useState, useEffect } from "react";
import { Box, Anchor, Select } from "grommet";
import ContactList from "./ContactList";
import Chat from "../Chatroom/";
import { getRolePublicProfile, findChatroomWithName, createchatroomListForRoles } from '../../library/parse'
import UserContext from '../../context/UserContext'
import { set } from "date-fns";

const Component = ({ onBackAnchor, chatroomIndex, chatroomList, activeSenderRoleObject, onChatroomSelected }) => {
  
  const [activeChatroom, setActiveChatroom] = useState(null)

  useEffect(() => {
    setActiveChatroom(chatroomList[chatroomIndex])
  }, [chatroomIndex, chatroomList])
  return (
    <Box height="100%" overflow="scroll">      
      { (
        !activeChatroom && <ContactList
          data={chatroomList}
          onClick={(item, index) => {
            onChatroomSelected && onChatroomSelected(item, index)
          }}
        />
      )}

      {activeChatroom && activeSenderRoleObject &&  <Anchor onClick={() => {
        onBackAnchor && onBackAnchor()
      }}>Back</Anchor>}
      

      {activeChatroom && activeSenderRoleObject && <Chat 
        senderRoleName={activeSenderRoleObject.object.get('name')}
        chatroom={activeChatroom.chatroom}
        chatroomName={activeChatroom.name}
      />} 
    </Box>
  );
};

export default Component;
